import * as React from "react"

import { arrayRange, DEG, round10, TAU, V3 } from "ts3dutils"

export const INCH = 25.4
export const fmtdeg = (x) => "" + round10(x / DEG, -1) + "Â°"
 

export const radiusFromSideWidth = (sides, sideWidth) =>
  sideWidth / 2 / Math.sin(TAU / sides / 2)
export const sideWithFromRadius = (sides, radius) =>
  radius * 2 * Math.sin(TAU / sides / 2)
export const centerToSideFromSideWidth = (
  sides,
  sideWidth,
) => sideWidth / 2 / Math.tan(TAU / sides / 2)
export const radiusFromCenterToSide = (
  sides,
  centerToSide,
) => centerToSide / Math.cos(TAU / sides / 2)
export const sideWidthFromCenterToSide = (
  sides,
  centerToSide,
) => centerToSide * 2 * Math.tan(TAU / sides / 2)

export function RegularPolygon({
  sides,
  radius,
  startAngle = 0,
  sideLength = undefined,
  ...props
}




) {
  if ((undefined !== sideLength) === (undefined !== radius)) {
    throw new Error("must set either sideLength or radius")
  }
  if (undefined === radius) {
    radius = radiusFromSideWidth(sides, sideLength)
  }
  const { x, y } = V3.polar(radius, startAngle)
  return (
    React.createElement('path', {
      d: dTpl`
        M${x},${y}
        ${arrayRange(0, sides).map(
          (i) => dTpl`L${V3.polar(radius, startAngle + i * (TAU / sides))}`,
        )}Z`,
      ...props,}
    )
  )
}

export function RotStep({
  id,
  children,
  count,
  stepDeg,
}




) {
  return (
    React.createElement(React.Fragment, null
      , React.createElement('g', { id: id,}, children)
      , arrayRange(0, count - 1).map((i) => (
        React.createElement('use', {
          key: i,
          xlinkHref: "#" + id,
          transform: `rotate(${(i + 1) * stepDeg} 0 0)`,}
        )
      ))
    )
  )
}

export function dTpl(
  strings,
  ...exps
) {
  const format = (x) =>
    "number" === typeof x
      ? "" + x
      : "string" === typeof x
      ? x
      : Array.isArray(x)
      ? x.map(format).join(" ")
      : x.x + "," + x.y
  let result = strings[0]
  for (let i = 0; i < exps.length; i++) {
    result += format(exps[i])
    result += strings[i + 1]
  }
  return result
}

export const openInNewTab = (url) => {
  const newWindow = window.open(url, "_blank", "noopener,noreferrer")
  if (newWindow) newWindow.opener = null
}
